@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  list-style: none;
}

body{
  background-color: #fff;
}
.App {
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  /*background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);*/
  overflow-x: hidden;
  font-family: Helvetica;
  /*background-image: url('./imgs/home.jpg');*/
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

header{
  display: flex;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

header h1{
  font-size: 3.5rem;
  text-align: center;
}

.content-wrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.Initial-Text{
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 1.3rem;
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.menu{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px 0 30px 0;
}

.menu .menu-link{
  color: white;
  text-decoration: none;
  padding: 0 15px;
  margin: 0 20px;
  font-size: 1.3rem;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.menu .menu-link:hover{
  transition: 0.8s;
  color: rgb(71, 129, 255);
  -webkit-text-decoration: underline #fff;
          text-decoration: underline #fff;
}

/* width */
::-webkit-scrollbar {
  display: none;
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
   background: #f1f1f100;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

@media (max-width: 700px)
{
  .menu{
    flex-direction: column;
    margin: 2px 0;
   }

   .menu .menu-link{
    padding: 10px 0;
   }
}
.Wellcome-Text{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 8px;
    padding-left: 6px;
    padding-right: 6px;
}

.Initial-Text{
    margin-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
}
.PhotoBox{
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 450px;
    height: 300px;
    border: 8px solid rgba(176, 176, 201, 0.74);
    margin: 20px 0 20px 0;
}

.PhotoBox:hover{
    border: 8px solid rgb(255, 255, 255);
    cursor: pointer;
    transition: 0.7s;
}

img{
    width: 100%;
    height: 100%;
}

.Photo-Text{
    position: absolute;
    bottom: 0;
    left: 0;
    color: #fff;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    font-size: 1.3rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: center;
    font-family: 'Roboto', sans-serif;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(43, 43, 44, 0.904);
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .modal-content{
    background-color: rgb(235, 235, 252);
    font-size: 1.2rem;
    font-family: 'Roboto', sans-serif;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    max-width: 80vw;
    max-height: 90vh;
    border-radius: 8px;
  }

  #closebtn{
    position: fixed;
    width: 45px;
    height: 30px;
    top: 20px;
    right: 30px;
    color: rgb(240, 255, 255);
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    font-size: 30px;
    transition: color 400ms;
    cursor: pointer;
  }

  #closebtn:hover{
    color: rgba(0, 0, 0, 0.795);
  }

  .fullscimg{
    max-width: 90vw;
    max-height: 90vh;
    border-radius: 0 0 8px 8px;
    border: 2px solid black;
  }

  @media (max-width: 700px)
{
    .modal-overlay {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(43, 43, 44, 0.904);
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    
      .modal-content{
        background-color: rgb(235, 235, 252);
        font-size: 1.2rem;
        font-family: 'Roboto', sans-serif;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        max-width: 90vw;
        max-height: 90vh;
        border-radius: 8px;
      }

      #closebtn{
        position: fixed;
        width: 45px;
        height: 30px;
        top: 190px;
        right: 20px;
        color: rgb(240, 255, 255);
        background-color: rgba(0, 0, 0, 0);
        border: 0;
        font-size: 30px;
        transition: color 400ms;
      }
}
